// shadow
.shadow {
    @include shadow;
}

.success {
    background: $secondary-green;
    color: $color-white;
    // background-color: ($secondary-green, 40%);
    // color: darken($secondary-green, 15%);
}

.danger {
    background: $tertiary-red;
    color: $color-white;
    // background-color: ($tertiary-red, 32%);
    // color: darken($tertiary-red, 15%);
}

.warning {
    background: $tetra-yellow;
    color: $color-white;
    // background-color: ($tetra-yellow, 27%);
    // color: darken($tetra-yellow, 15%);
}

.hide {
    animation: fadeOut ease-in .5s forwards;
}
