// card 
.card__container {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
}

.card {
    box-shadow: 0 1px 4px rgba(0,0,0,.25);
    border-bottom-left-radius: 3px;
    border-bottom-right-radius: 3px;

    &__head {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: .5rem 1.25rem; // 10px 20px
        background: $primary-blue;
        color: $color-white;
        border-top-left-radius: 4px;
        border-top-right-radius: 4px;
    }

    &__id {
        font-size: 1.375rem;
    }

    &__body {
        padding: 1.125rem 1.25rem;
    }

    &__text {
        font-size: .875rem;
        line-height: 1.5rem;
    }
}

.card__grid {
    flex: 0 1 calc(33.33333333333333% - 1.25rem);
    margin: .625rem;

    @include respond(tab-port) {
        flex: 0 1 calc(50% - 1.25rem);
        margin: .625rem;
    }

    @include respond(phone) {
        flex: 0 1 100%;
        margin: .625rem 0;
    }
}

.card__list {
    flex: 1 1 100%;
    margin: .625rem;

    @include respond(phone) {
        margin: .625rem 0;
    }
}