// alert 
.alert {
    min-width: calc(12.5rem - 1.875rem);
    border-radius: 3px;
    position: fixed;
    right: .9375rem;
    bottom: 1.5625rem;

    &__text {
        text-shadow: 0 1px 1px rgba($color-black, .2);
        padding: .625rem 1rem;
        font-weight: $font-bold;
        font-size: .75rem;
        margin-top: .625rem;
        box-shadow: 0 1px 1px rgba(0,0,0,.1);
        animation: fadeIn ease-in .3s forwards;
    }
}