// tasks
.tasks {
    display: flex;
    align-items: center;
    padding: 5px 0px;
    margin-bottom: .9375rem;

    &__name {
        font-size: 1.375rem;
        font-weight: $font-bold;
        align-self: center;
        margin-right: auto;
    }

    &__link {
        display: flex;
        align-items: center;
    }

    &__delete {
        text-decoration: none;
        margin-right: .75rem;
    }

    &__grid {
        text-decoration: none;
    }
}