// nav
.navbar {
    padding: .625rem 0;
    display: flex;
    align-items: center;

    &__brand {
        display: inline-flex;
        align-items: center;
        text-decoration: none;
        color: $color-black;
        font-weight: $font-bold;
        font-size: .9375rem;
        text-transform: uppercase;
        margin-right: auto;
    }

    &__logo {
        width: 1.875rem;
        height: 1.875rem;
        margin-right: .5rem;
    }

    &__themes {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        list-style-type: none;

        &--item {
            cursor: pointer;
            height: 1.875rem;
            width: 1.875rem;
            border-radius: 50%;

            &:not(:last-child) {
                margin-right: .625rem;
            }
        }
    }
}

.blue {
    background: $primary-blue;
}
.yellow {
    background: $tetra-yellow;
}
.red {
    background: $tertiary-red; 
}
.green {
    background: $secondary-green;
}