// input
.input {
    display: flex;
    flex-direction: column;
    margin-top: 2.5rem;
    margin-bottom: 1.875rem;

    @include respond(phone) {
        margin-top: 1.75rem;
    }

    &__div {
        display: flex;
        align-items: center;
    }

    &__new {
        flex: 1 1 calc(100% - .9375rem);
        margin-right: 1.25rem;
        outline: none;
        border: none;
        border-bottom: 1px solid $color-gray;
        padding: 0 .625rem .625rem .625rem;
        font-size: .875rem;
        margin-bottom: 1.875rem;

        @include respond(phone) {
            flex: 0 1 calc(100% - 1.25rem);
            margin-bottom: 1.875rem;
        }
    }

    &__filter {
        flex: 1;
        outline: none;
        border: none;
        border-bottom: 1px solid $color-gray;
        padding: 0 .625rem .625rem .625rem;
        font-size: .875rem;

        @include respond(phone) {
            flex: 1 1 100%;
            
        }
    }

    &__submit {
        flex-grow: 0;
        flex-shrink: 0;
        cursor: pointer;
        outline: none;
        border: none;
        background: $primary-blue;
        color: $color-white;
        padding: .625rem 1rem;
        border-radius: 5px;
        font-weight: $font-bold;
        font-size: .75rem;
        margin-bottom: 1.75rem;
    }

    @include respond(phone) {
        flex-wrap: wrap;
    }
}